.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(51, 51, 51);  
    margin:20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding: 10px;
}

.image{
    width: 250px;
    height:250px;
    margin: 20px;
    border-radius:10px;
}
.list {
    font-family:sans-serif;
}
.calories{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 25px;
}