.App{
  min-height: 100vh;
  background: #356563;
}
.search-form{
  min-height: 10vh;
  display:flex;
  justify-content: center;
  align-items:center;
}
.search-bar{
  min-width:50%;
  min-height: 25px;
  border:none;
  padding:5px;
}
.search-button{
  background: #970A44;
  color:white;
  padding:10px 20px; 
  border: none;
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
h1{
  text-decoration: none;
  text-align: center;
  margin-top: 0px;
  padding: 15px;
  font-family: sans-serif;
}